.hero {
    width: 100%;
    min-height: 100vh;
    background: linear-gradient(rgba(99, 55, 50, 0.7), rgba(1, 34, 99, 0.7)), url('../../assets/img6.jpg');
    background-size: cover;
    background-position: center;
    color: #ffffff; 
    display: flex;
    align-items: center;
    justify-content: center;
}

.hero-text{
    text-align: center;
    max-width: 800px;
}

.hero-text h1{
    font-size: 50px;
    font-weight: 600;
}

.hero-text p{
    max-width: 700px;
    margin: 10px auto 20px;
    line-height: 1.4
}

/* MEDIA QUERY */

@media(max-width:850px){
    .hero-text h1{
        font-size: 40px;
    }
}

@media(max-width:650px){
    .hero-text h1{
        font-size: 30px;
        max-width: 400px;
        margin: auto
    }
}

@media(max-width:350px){
    .hero-text h1{
        font-size: 20px;

    }
    .hero-text p{
        font-size: 10px;
        margin: 15px auto
    }

}
